@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,700);
body {
  height: 100vh;
  width: 100vw;
  font-family: 'Raleway', sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; }

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  outline: none; }

.App {
  width: 50vw;
  height: 50vh;
  position: relative; }

.Carousel {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden; }
  .Carousel .Progress-bar {
    width: 0;
    height: 5px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 255, 0.25);
    transition: all 1s linear; }
  .Carousel .hidden {
    opacity: 0; }
  .Carousel .Controls {
    font-size: 40px;
    color: white;
    position: absolute;
    bottom: 5px;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: rgba(0, 0, 0, 0.5);
    width: 100%; }
    .Carousel .Controls > * {
      padding: 10px;
      cursor: pointer; }
  .Carousel__slides > * {
    position: absolute;
    right: 0;
    top: 0;
    opacity: 1;
    transition: all .5s ease; }
  .Carousel .Slide {
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-position: center center;
    background-size: cover; }
    .Carousel .Slide h2 {
      font-size: 50px;
      font-weight: 300;
      color: white;
      background: rgba(0, 128, 0, 0.25);
      margin-bottom: 10px;
      padding: 5px 30px; }
    .Carousel .Slide p {
      font-size: 35px;
      color: white;
      background: rgba(0, 128, 0, 0.25);
      padding: 5px 30px; }

